@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,700;0,800;1,300;1,400;1,500;1,600&family=Roboto:ital,wght@0,100;0,300;0,400;1,100;1,300;1,400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Roboto', sans-serif;
  /* font-family: 'Montserrat', sans-serif; */
  font-size: 1rem;
  overflow-x: hidden
}

.container {
  width: 100vw;
  margin: 0 auto;
  box-sizing: border-box;
}

li {
  list-style: none;
}

.links {
  text-decoration: none;
}

section {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

h1, h3 {
  margin-bottom: 1rem;
}

button {
  padding: .5rem 2rem;
  outline: none;
  border: 1px solid transparent;
}
 a {
   text-decoration: none;
 }

 @media screen and (max-width: 1200px) {
   section {
     position: relative;
     height: auto;
     padding: 4rem 0;
   }
 }

#header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 15vh;
    background-color: #fff;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
}

#logo {
    margin: 0 15rem 0 5rem;
    font-weight: 800;
    font-size: 2rem;
}

#nav {
    margin: 0 4rem 0 0;
    width: 90%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

#nav ul {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: space-around;
}

#burger {
    display: none;
}

#mobile-menu {
    display: none;
}

.links {
    padding: 0.5rem 1rem;
    border-bottom: 2px solid transparent;
}

.links:hover {
    background-color: orangered;
    color: #fff;
}

#burger {
    display: none;
    width: 50px;
    height: 50px;
}
#inner-burger {
    width: 30px;
    height: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.bars {
    width: 100%;
    height: 0.2rem;
    background-color: #494949;
}

#product, #companys {
    border: 1px solid #fff;
    position: absolute;
    top: 10vh;
    display: none;
    justify-content: center;
    align-items: center;
}

#product ul, #companys ul {
    /* width: auto; */
    height: 25vh;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: #f5f5f5;
    /* padding: 1rem; */
    width: 13rem;
}

.nav ul:hover #product, .nav ul:hover #companys {
    display: block;
}

.sub-links {
    color: #494949;
}

.sub-links:hover {
    color: #ff4500;
}

.nav ul li a {
    padding: 0.5rem 1rem;
    color: #494949;
    position: relative;
}

.nav ul li a:hover {
    color: #ff4500;
}

#com {
    display: flex;
    align-items: center;
}

.fa-chevron-down {
    margin-left: .2rem;
}

#sign-up {
    background-color: #ff4500;
    padding: .5rem 2rem;
    color: #fff;
}

#sign-up:hover {
    background-color: #494949;
}



@media screen and (max-width: 1200px) {
    #header {
        width: 100%;
    }

    #logo {
        margin: 0 0 0 2rem;
    }
    #burger {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 2rem;
        z-index: 10;
    }
    #nav {
        position: absolute;
        top: 0;
        right: -64px;
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        justify-content: space-around;
        align-items: center;
        transform: translateX(100%);
        transition: all .5s ease-in-out;
        background-color: whitesmoke;
    }
    #nav-open {
        transform: translateX(0%);
    }
    .links {
        padding: 0.5rem 1rem;
    }
    .links:hover {
        background-color:transparent;
        border-bottom: 2px solid #ff4500;
    }
    #product, #companys {
        width: auto;
        height: auto;
        z-index: 10;
        position: relative;
        top: 0;
    }

    #product ul, #companys ul {
        display: flex;
        justify-content: space-around;
        align-items: center;
        position: absolute;
        /* padding: 1rem; */
        border: #fff solid 2px;
    }
    .nav ul:hover #product, .nav ul:hover #companys {
        display: flex;
    }
    #sign-up {
        background-color: transparent;
        color: #494949;
    }
    #sign-up:hover {
        background-color: transparent;
        color: #ff4500;
    }

}
#one {
    background-color: #f5f5f5;
}

#landing {
    height: 100%;
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

#landing h1 {
    margin-bottom: 1rem;
    font-size: 3rem;
    color: #ff4500;
    width: auto;
    width: 65%;
}

#landing p {
    margin-bottom: 1rem;
    line-height: 1.8;
    color: #494949;
    width: auto;
}

#dig-deep button {
    background-color: #494949;
    color: #fff;
}

#dig-deep:hover {
    color: #494949;
    background-color: #fff;
    border: 1px solid #494949;
    outline: none;
}

@media screen and (max-width: 1200px) {
    #landing h1, #landing p {
        width: 90vw;
    }
}
#products {
    background-color: #fff;
    width: 100%;
    /* background-image: url(../../../assets/img/undraw_product_tour_foyt.svg);
    background-position: right 50%; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.inner-products {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.grey {
    background-color: #f5f5f5;
}

.product__img {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.product__img img {
    width: 100%;
}

.product__text {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.product__text h2 {
    text-align: center;
    margin-bottom: 1rem;
    width: auto;
    color: orangered;
    font-size: 2rem;
}

.product__text p {
    text-align: center;
    width: auto;
    line-height: 1.6;
    font-size: 1.3rem;
    color: #494949;
}

#product-one, 
#product-three {
    display: flex;
    align-items: center;
}

#product-one p, 
#product-three p,
#product-one h2, 
#product-three h2 {
    text-align: right;
    width: 25rem;
}

#product-two {
    display: flex;
    align-items: center;
}

#product-two p,
#product-two h2 {
    text-align: left;
    width: 25rem;
}

.read-more {
    color: orangered;
    font-size: .8rem;
    font-weight: 900;
}

@media screen and (max-width: 1200px) {
    .inner-products {
        flex-direction: column;
        width: 90vw;
        justify-content: center;
        align-items: center;
    }
    .product__text h2,
    .product__text p {
        width: 90vw;
    }
    #product-one, #product-three {
        margin-top: 1rem;
    }

    #product-two {
        margin-bottom: 1rem;
    }

    #product-one p, 
    #product-three p,
    #product-one h2, 
    #product-three h2 {
        text-align: center;
        width: 90vw;
    }
    #product-two p, 
    #product-two p,
    #product-two h2, 
    #product-two h2 {
        text-align: center;
        width: 90vw;
    }
}
#five {
    background-color: #f5f5f5;
}

#newsletter {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#newsletter h3 {
    color: #ff4500;
    font-size: 2rem;
}

#input {
    width: 50%;
    border: 1px solid grey;
    background-color: #494949;
    padding: .2rem;
    display: flex;
    justify-content: space-between;
}

#input input {
    height: 100%;
    width: 80%;
    outline: none;
    background-color: #494949;
    border: none;
    padding-left: 1rem;
}

#input button {
    width: 19%;
    background-color: #ff4500;
    color: #fff;
    cursor: pointer;
}

@media screen and (max-width: 1200px) {
    #newsletter {
        width: 90vw;
    }
    #input input {
        width: 70%;
        padding: 0.5rem;
    }

    #input button {
        width: 30%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    #newsletter h3 {
        width: 90vw;
        text-align: center;
    }
}
#home {
    position: relative;
    top: 15vh;
}
#six {
    background-color: #494949;
    position: relative;
    top: 15vh;
}

#footer {
    position: relative;
    top: 0;
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
}

#contact {
    width: 20%;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    z-index: 7;
    justify-content: center;
    height: 30rem;
}

.contact p, #company p {
    color: #000;
    margin: 0;
}

.contact img {
    margin-right: 1rem;
}

.contact {
    display: flex;
    height: 3rem;
    z-index: 8;
    align-items: center;
}

#company {
    width: 20%;
    height: 30rem;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}

#company p {
    margin-bottom: 1rem;
    font-size: 1rem;
    width: 20rem;
}

#socials {
    display: flex;
    justify-content: flex-start;
}

.socials {
    width: 3rem;
    height: 3rem;
    margin: 0 1rem 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #272727;
}

.socials img {
    width: 1.2rem;
}

#company h3 {
    margin-bottom: 1rem;
    font-weight: 600;
}

@media screen and (max-width: 1200px) {

    #footer {
        position: relative;
        top: 0;
        height: auto;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    
    /* #inner-footer {
        height: 50vh;
        flex-direction: column;
        justify-content: center;
        padding: 4rem 0;
        align-items: center;
    } */

    #contact {
        height: auto;
        margin: 2rem 0;

    }

    #company, #contact {
        height: auto;
        width: auto;
        margin: 2rem 0;
        align-items: center;
    }

    #company p {
        width: 70vw;
        overflow-wrap: break-word;
        text-align: center;
    }
    #socials {
        width: 90vw;
        justify-content: center;
        margin-right: -1rem;
    }
    .contact {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 1rem 0;
        text-align: center;
        width: auto;
    }

    .contact img {
        margin: 0;
    }

    #company h3 {
        text-align: center;
    }
}
#landing-image {
    width: 60%;
    height: 60%;
    /* background-image: url(../../../../assets/img/welcome.svg); */
