#form {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
    padding: 2rem 0 0 0;
}

#form h1 {
    width: 90%;
    color: #ff4500;
    letter-spacing: .1rem;
    font-size: 1.5rem;
    padding: 0 0 0 1.5rem;
}

#inner-form {
    padding: 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    align-items: center;
    width: 90%;
}

.form-control {
    margin-bottom: 1rem;
    width: 100%;
}

.form-control label {
    display: block;
    margin-bottom: .5rem;
    font-size: .9rem;
    font-weight: 600;
    color: #494949;
}

.form-control input {
    padding: .5rem;
    width: 100%;
    border: 1px solid transparent;
    border: 1px solid #f5f5f5;
    outline: none;
}

.form-control button {
    background-color: #494949;
    color: #fff;
    cursor: pointer;
    width: 100%;
}

.form-control button:hover {
    background-color: #ff4500;
}

#btn {
    margin-bottom: 0;
}

.switch p {
    color: #494949;
    font-size: .8rem;
    font-weight: 900;
}

#switch {
    color: #ff4500;
}

@media screen and (max-width: 1200px) {
    #inner-form {
        grid-template-columns: 1fr;
    }
}
